import { isPlatformBrowser } from '@angular/common';
import { Component, Inject, ViewChild, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { Sponsor } from '@app/shared/interfaces/sponsor';

@Component({
  selector: 'app-resources',
  templateUrl: './resources.component.html',
})
export class ResourcesComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute
  ) {
  }
  @ViewChild('discordLinkInput') discordField: any;

  discordLink: string = "https://discord.gg/ebkUh4Daey";
  resourcesData:any;
  isCopying: boolean = false;
  showCopiedMessage: boolean = false;

  async ngOnInit() {
    // this.resourcesData = await this.activatedRoute.snapshot.data["resources"];
  }

  handleDiscordButtonClick():void {
    if (!this.discordField) {
      return;
    }

    try {
      if (this.isCopying) {
        return;
      }

      this.isCopying = true;

      const text = this.discordField.nativeElement.value;

      navigator.clipboard.writeText(
        text
      );

      this.showCopiedMessage = true;
      setTimeout(() => {
        this.showCopiedMessage = false;
        this.isCopying = false;
      }, 1000)


    } catch (e) {
      console.log("Caught: ", e);
    }
  }
}
